            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="inStoreSure"> </el-button>
            </span>
        </el-dialog>
        <!--     operateType -->
        <el-dialog
