                 <el-table-column label="" width="50" prop="id">
                </el-table-column>
                <el-table-column label="" width="180">
                    <template scope="scope">
                        <span>{{scope.row.date}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="180">
                    <template scope="scope">
                        <span>{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="">
                    <template scope="scope">
                        <span>{{scope.row.address}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" :label="operateType + ''">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.number" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
                 <el-table-column prop="scope" label="">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.remark" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="storeDiaogVisible= false"> </el-button>
                <el-button type="primary" @click="storeSureChange"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <!-- <el-dialog 
            title=""
            :visible.sync="storeDiaogVisible"
            width="70%"
            :before-close="handleClose">
         <el-table :data="secondInStoreTableData" 
                class="tb-edit" style="width: 100%" 
                highlight-current-row >
                 <el-table-column label="" width="50" prop="id">
                </el-table-column>
                <el-table-column label="" width="180">
                    <template scope="scope">
                        <span>{{scope.row.date}}</span>
