                </template>
            </el-table-column>
        </el-table> -->
    </div>
</template>

<script>
  export default {
    data() {
        return {
            tableData: [{
                id:'1',
                date: '',
                name: '-4',
                address: '10000'
                },{
                id:'2',
                date: '',
                name: '-4',
                address: '10000'
                },
                {
                id:'3',
                date: '',
                name: '-2',
                address: '10000'
                },
                {
                id:'4',
                date: '',
                name: '-8',
                address: '10000'
                },
                {
                id:'5',
                date: '',
                name: '-12',
                address: '10000'
                },
                {
                id:'6',
                date: '',
                name: '-1',
                address: '10000'
                },
                {
                id:'7',
                date: '',
                name: '-3',
                address: '10000'
            }],
            multipleSelection: [],
            dialogVisible:false,
            inStoreTableData:[{
                date: '',
                name: '',
                address: ''
            }],
            secondInStoreTableData:[

            ],
            storeDiaogVisible:false,
            operateType:''
        }
    },

    methods: {
        // 1 
        storeOperate(index) {
            if (index*1 === 1) {
                this.operateType= ''
                if (this.multipleSelection.length === 0) {
                    this.dialogVisible = true
                    return
                }
            }
            else {
                this.operateType = ''
                if (this.multipleSelection.length === 0) {
                    return
                }
            }
            this.storeDiaogVisible = true
            const target = this.multipleSelection.map(item => {
                return Object.assign({},item,{
                    number:'',
                    remark:''
                })
            })
            this.secondInStoreTableData = target
        },
        handleClose(done) {
            done();
            // this.$confirm('')
            //     .then(_ => {
            //         done();
            //     })
            //     .catch(_ => {});
        },
        // 1-
        inStoreTableDeleteRow(index, row) {
            if(this.inStoreTableData.length === 1) {
                return
            }
            this.inStoreTableData.splice(index,1)
        },
        inStoreTableAddRow() {
             this.inStoreTableData.push({
                date: '',
                name: '',
                address: ''
             })
        },
        inStoreSure() {
            this.dialogVisible = false
            this.$notify({
                message:"",
                type:'success'
            })
            const target = this.inStoreTableData.map(item =>{
                return  Object.assign({},item,{
                    a:''
                })
            })
            this.tableData.push(...target)
        },
        // inStoreSureChange
        storeSureChange() {
            this.storeDiaogVisible = false
            this.secondInStoreTableData.map(item => {
                const id = item.id
                let value
                if(this.operateType ==='') {
                    value = item.address*1 - item.number*1
                }
                else {
                    value = item.address*1 + item.number*1
                }
                this.tableData.map(row => {
                    if(row.id === id) {
                        row.address = value
                    }
                })
            })
             this.$notify({
                message:'',
                type:'success'
            })
        },
        // toggleSelection(rows) {
        //     if (rows) {
        //         rows.forEach(row => {
        //             this.$refs.multipleTable.toggleRowSelection(row);
        //         });
        //     } else {
        //         this.$refs.multipleTable.clearSelection();
        //     }
        // },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            this.changeableTableData = val
        },
        handleCurrentChange(row, event, column) {
            // console.log(row, event, column, event.currentTarget)
        },
        handleEdit(index, row) {
            // console.log(index, row);
            this.inStoreTableData.slice(index,1)
        },
        handleDelete(index, row) {
            this.changeableTableData.splice(index,1)
        },
        handeleAdd() {
            this.changeableTableData.push( {
                date: '',
                name: '',
                address: ''
            })
        }
    }
  }
</script>