<template>
    <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.11 510E /
                </el-col>
            </el-row>
           <!-- button -->
            <el-row class="mrbt">
                <el-col :span="2">
                    <el-button size="mini" type='warning' icon="el-icon-plus" @click="handleAddTicket"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-dialog title="/" :visible.sync="dialogFormVisible">
                <el-form 
                    :model="dialogFormData" 
                    :rules="dialogFormDataRules"
                    ref='dialogFormData'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="" prop='cardPhysicsType'>
                                <el-input v-model="dialogFormData.cardPhysicsType" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="ID" prop='cardIssue'>
                                <el-input v-model.number="dialogFormData.cardIssue" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="" prop='cardType'>
                                <el-input v-model.number="dialogFormData.cardType" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="" prop='prePaymentAttribute'>
                                <el-input v-model.number="dialogFormData.prePaymentAttribute" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="ID" prop='deriveProductId'>
                                <el-input v-model.number="dialogFormData.deriveProductId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="" prop='prePaymentMoney'>
                                <el-input v-model="dialogFormData.prePaymentMoney" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="" prop='cardStatus'>
                                <el-input v-model="dialogFormData.cardStatus" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="" prop='cardNumber'>
                                <el-input v-model="dialogFormData.cardNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="" prop='transportationMode'>
                                <el-input v-model="dialogFormData.transportationMode" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="" prop='boxId'>
                                <el-input v-model="dialogFormData.boxId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('dialogFormData')">
                        
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        
                    </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-row class='table-history mrbt' v-show="tableVisble">
                <el-table :data="dialogForm2table" style="width: 100%" size='mini' max-height="250">
                    <el-table-column fixed prop="cardPhysicsType" label="" width="100">
                    </el-table-column>
                    <el-table-column prop="cardIssue" label="ID" width="100">
                    </el-table-column>
                    <el-table-column prop="cardType" label="" width="100">
                    </el-table-column>
                    <el-table-column prop="prePaymentAttribute" label="" width="80">
                    </el-table-column>
                    <el-table-column prop="deriveProductId" label="ID" width="100">
                    </el-table-column>
                    <el-table-column prop="prePaymentMoney" label="" width="80">
                    </el-table-column>
                    <el-table-column prop="cardStatus" label="" width="80">
                    </el-table-column>
                    <el-table-column prop="cardNumber" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="transportationMode" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="boxId" label="" width="120">
                    </el-table-column>
                    <el-table-column fixed="right" label="" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2table)" type="danger" size="small">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row>
                <el-col :span="16" :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px" :rules="mainFormRules">
                        <el-form-item label="/" prop='cardAndBoxDetailNumber'>
                            <el-input v-model="mainForm.cardAndBoxDetailNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="deliveryBatchNumber">
                            <el-input v-model="mainForm.deliveryBatchNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="dataOfDispatch">
                            <el-input v-model="mainForm.dataOfDispatch"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="DispatchWay">
                            <el-radio-group v-model="mainForm.DispatchWay">
                                <el-radio :label="1" size="mini" border></el-radio>
                                <el-radio :label="2" size="mini" border></el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="/" prop="locationType">
                            <el-radio-group v-model="mainForm.locationType">
                                <el-radio :label="1" size="mini" border></el-radio>
                                <el-radio :label="2" size="mini" border></el-radio>
                                <el-radio :label="3" size="mini" border></el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="/" prop="locationCode">
                            <el-input v-model="mainForm.locationCode"></el-input>
                        </el-form-item>
                        <el-form-item label="ID" prop="DeliveryOpId">
                            <el-input v-model="mainForm.DeliveryOpId"></el-input>
                        </el-form-item>
                        <el-form-item :label="operateType+''" prop="inOutWarehouseCardNumber">
                            <el-input v-model.number="mainForm.inOutWarehouseCardNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="dispatchNumber">
                            <el-input v-model="mainForm.dispatchNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="emptyBoxes">
                            <el-input v-model="mainForm.emptyBoxes"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class="iconfont antd-icon-edit-square"></i>
                                {{operateType}}
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
export default {
    name:'',
        data() {
            return {
                dialogFormVisible: false,
                dialogFormData:{
                    cardPhysicsType:'',
                    cardIssue:'',
                    cardType:'',
                    prePaymentAttribute:'',
                    deriveProductId:'',
                    prePaymentMoney:'',
                    cardStatus:'',
                    cardNumber:'',
                    transportationMode:'',
                    boxId:''
                },
                dialogFormDataRules:{
                    cardPhysicsType: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    cardIssue: [
                        { required: true, message: 'ID', trigger: 'change' },
                        { type: 'number', message: 'ID' }
                    ],
                    cardType: [
                        { required: true, message: 'id', trigger: 'change' },
                        { type: 'number', message: 'id' }
                    ],
                    prePaymentAttribute: [
                        { type: 'number', required: true, message: '', trigger: 'change' }
                    ],
                    deriveProductId: [
                        { type: 'number', required: true, message: 'ID', trigger: 'change' }
                    ],
                    prePaymentMoney: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    cardStatus: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    cardNumber: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    transportationMode: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    boxId: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                },
                dialogForm2table:[

                ],
                mainForm: {
                    cardAndBoxDetailNumber: '',
                    deliveryBatchNumber: '',
                    dataOfDispatch: '',
                    DispatchWay: '',
                    locationType: '',
                    locationCode: '',
                    DeliveryOpId: '',
                    inOutWarehouseCardNumber: '',
                    dispatchNumber:'',
                    emptyBoxes:'',
                    cardAndBoxDetails:[],
                },
                mainFormRules: {
                    cardAndBoxDetailNumber: [
                        { required: true, message: '', trigger: 'change' },
                        { min: 1, max: 5, message: ' 3  5 ', trigger: 'change' }
                    ],
                    deliveryBatchNumber: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    dataOfDispatch: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    DispatchWay: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    locationType: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    locationCode: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    DeliveryOpId: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    inOutWarehouseCardNumber: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    dispatchNumber: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    emptyBoxes: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    cardAndBoxDetails: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                }
            }
       },
    computed: {
        tableVisble() {
            return this.dialogForm2table.length === 0 ? false : true
        },
        operateType() {
            const length = this.mainForm.DispatchWay.length
            if (length === 0) {
                return ''
            }
            else {
                if (this.mainForm.DispatchWay === 1) {
                    return ''
                }
                else if (this.mainForm.DispatchWay === 2) {
                    return ''
                }
                else {

                }
            }
        }
    },
    methods: {
        handleAddTicket() {
            this.dialogFormVisible = true
        },
        dialogFormSubmit(formName) {
            let addTableData = this.dialogFormData
            let target = Object.assign({}, addTableData)
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.dialogForm2table.push(target)
                    vm.resetForm('dialogFormData')
                    vm.dialogFormVisible = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            
            this.$refs[formName].resetFields();
            
        },
        mainFormSubmit(formName) {
            const length = this.dialogForm2table.length
            if (length === 0) {
                this.$message({
                    type: 'error',
                    message: '',
                    duration: 2000
                })
                return
            }
            this.mainForm.cardAndBoxDetails = this.dialogForm2table
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                     vm.$notify({
                            type: 'success',
                            message: '',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                        vm.dialogForm2table = []
                    // vm.$axios('/success').then(res => {
                       
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
     
        handleCreateForm() {

        }
    }
}
</script>
