<template>
   <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.10 510D /
                </el-col>
            </el-row>
            <el-row>
                    <el-col :span='16' :offset="2">
                        <el-form 
                            ref="form" 
                            :model="form"
                            status-icon 
                            :rules="rules"
                            label-width="160px">
                            <el-form-item label=""  prop='operateType'>
                                <el-radio-group v-model="form.operateType">
                                    <el-radio :label="1" border size="mini"></el-radio>
                                    <el-radio :label="2" border size="mini"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="ID" prop='boxId'>
                                <el-input v-model="form.boxId"></el-input>
                            </el-form-item>
                            <el-form-item label="ID" prop='devId'>
                                <el-input v-model="form.devId"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop='cardPhysicsType'>
                                <el-input v-model="form.cardPhysicsType"></el-input>
                            </el-form-item>
                            <el-form-item label="ID" prop='cardIssue'>
                                <el-input v-model="form.cardIssue"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop='cardType'>
                                <el-input v-model="form.cardType"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop='prePaymentAttribute'>
                                <el-input v-model="form.prePaymentAttribute"></el-input>
                            </el-form-item>
                            <el-form-item label="ID" prop='deriveProductId'>
                                <el-input v-model="form.deriveProductId"></el-input>
                            </el-form-item>
                            <el-form-item :label="computedNum1" prop='box1Num'>
                                <el-input v-model="form.box1Num"></el-input>
                            </el-form-item>
                            <el-form-item :label="computedNum2" prop='box2Num'>
                                <el-input v-model="form.box2Num"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitForm('form')">
                                    <i class="iconfont antd-icon-appstore"></i>
                                    {{operateType}}
                                </el-button>
                                <el-button type='warning' @click="resetForm('form')"></el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </div>
   </transition>
</template>
<script>
    export default {
        name:'',
        data() {
            return {
                form: {
                    operateType: '',
                    boxId: '',
                    devId: '',
                    cardPhysicsType: '',
                    cardIssue: '',
                    cardType: '',
                    prePaymentAttribute: '',
                    deriveProductId: '',
                    box1Num: '',
                    box2Num:''
                },
                rules:{
                    operateType: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    boxId: [
                        { required: true, message: 'ID', trigger: 'change' },
                        // { type: 'number', message: 'ID' }
                    ],
                    devId: [
                        { required: true, message: 'id', trigger: 'change' },
                        // { type: 'number', message: 'id' }
                    ],
                    cardPhysicsType: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    cardIssue: [
                        {  required: true, message: 'ID', trigger: 'change' }
                    ],
                    cardType: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    prePaymentAttribute: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    deriveProductId: [
                        { required: true, message: 'ID', trigger: 'change' }
                    ],
                    box1Num: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    box2Num: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                }
            }
        },
        computed: {
            // operateType:function() {
            //     return this.form.operateType?this.form.operateType : ''
            // },
            operateType: function () {
                const length = this.form.operateType.length
                if (length === 0) {
                    return ''
                }
                else {
                    if (this.form.operateType === 1) {
                        return ''
                    }
                    else if (this.form.operateType === 2) {
                        return ''
                    }
                    else {

                    }
                }
            },
            computedNum1:function() {
                if(this.operateType == ''){
                    return ''
                }
                else if (this.operateType == '') {
                    return ''
                }
                else {
                    return '1'
                }
            },
            computedNum2:function() {
                if (this.operateType == '') {
                    return ''
                }
                else if (this.operateType == '') {
                    return ''
                }
                else {
                    return '2'
                }
            }
        },
        methods: {
            submitForm:function(formName) {
                let postData = this.form
                const vm = this
                this.$refs[formName].validate(function(valid){
                    if (valid) {
                         vm.$notify({
                            type: 'success',
                            message: '',
                            duration: 2000
                        })
                        vm.resetForm('form')
                        // vm.dialogForm2tableCardbox = []
                        // vm.dialogForm2tableCard = []
                        // this.$axios('./get/add').then((res)=>{

                        // }).cath((error) => {

                        // })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm: function(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>
